.loading {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    background: #fff url(../../img/loading.gif)no-repeat center;
    background-size: cover;
}

.items-container {
    width: 100%;
    height: 600px;
    max-height: 400px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: rgba(49, 61, 102, 0.473);
}