.container {
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background: url(/test-app/static/media/oboitut.com_1528317218.cdf1ae39.png)no-repeat center;
    background-size: cover;
    transition: .5s;
}

.container:hover{
    background-size: 110%;
}
.loading {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    background: #fff url(/test-app/static/media/loading.866cb20b.gif)no-repeat center;
    background-size: cover;
}

.items-container {
    width: 100%;
    height: 600px;
    max-height: 400px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    background: rgba(49, 61, 102, 0.473);
}

.item{
    width: 18%;
    max-width: 200px;
    height: 500px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    background: rgba(233, 236, 234, 0.575);
    border-radius: 10px;
    transition: .3s;
    overflow: auto;
}

.item:hover{
    background: rgba(233, 236, 234, 0.747);
    cursor: pointer;
}

.photo{
    width: 120px;
    height: 120px;
    margin-top: 20px;
    border-radius: 50%;
    background: rgb(117, 105, 105);
}

.full-name{
    font-family: 'Patrick Hand', cursive;
    color: rgba(51, 59, 59, 0.849);
}

.user-age{
    font-family: 'Patrick Hand', cursive;
    color: rgba(51, 59, 59, 0.849);
}
.pagination{
    margin-top: 5vh;
}

.pagination button{
    padding: 15px;
    background: rgba(255, 254, 254, 0.521);
    border: .5px solid rgba(203, 207, 207, 0.541);
    font-family: 'Patrick Hand', cursive;
    font-size: 14px;
    color: rgba(63, 66, 66, 0.849);
    margin: .5px;
}

.pagination button:focus{
    outline: transparent;
}

.pagination button:active{
    background: rgba(156, 152, 152, 0.856);
}

.pagination button:hover{
    cursor: pointer;
    background: rgba(155, 168, 173, 0.507);
    border-color: transparent;
}

.pagination button:active{
    background: rgba(50, 52, 53, 0.507);
}

#prevBtn{
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
}

#prevBtn:hover{
    background:rgba(49, 61, 102, 0.473);
    border-color: transparent;
    color: rgb(210, 215, 216);
}

#prevBtn:active{
    background:rgba(174, 177, 187, 0.473);
}

#nextBtn{
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
}

#nextBtn:hover{
    background:rgba(49, 61, 102, 0.473);
    border-color: transparent;
    color: rgb(210, 215, 216);
}

#nextBtn:active{
    background:rgba(174, 177, 187, 0.473);
}


